import PageButton from '../../components/button.vue';

export default {
	components: {

		PageButton
	},
	data() {
		return {
			temp: {
				withdrawPassword: '',
				withdrawPasswordS: ''
			},
		}
	},
	methods: {
		submit:function(){
			let that = this;
			if(!that.$util.isEmpty(that.temp.withdrawPassword)){
				uni.showToast({
					title: '请输入提现密码',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.withdrawPasswordS)){
				uni.showToast({
					title: '请第二次输入提现密码',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(that.temp.withdrawPassword != that.temp.withdrawPasswordS){
				uni.showToast({
					title: '请输入相同密码',
					icon:'none',
					duration: 2000
				});
				return;
			}
			
			that.$util.request({
				url: "/didi-doctor/doctorinfo/setWithdrawPassword",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.setStorageSync('doctorInfo', res.data);
					uni.showToast({
						title: '设置成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 500)
				},
				error: function() {}
			})
		}
	}
}
